import React, {useEffect, useState} from 'react'
import { Link, useNavigate } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CInputGroup,
  CInputGroupText,
  CRow,
  CSmartTable
} from '@coreui/react-pro'
import CIcon from '@coreui/icons-react'
import { cilDelete, cilEyedropper, cilLockLocked, cilLowVision, cilPen } from '@coreui/icons'
import axios from 'axios'
import { toast } from 'react-toastify'
import { Role } from 'src/constant'

const Brands = () => {
  const navigate = useNavigate();
  const [brands, setBrands] = useState([])

  useEffect(() => {
    axios.get('brand/findAll', {headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }}).then(response => setBrands(response.data.data))
    .catch(error => console.error('error fetching brands', error));
  }, [])

  const deleteBrandAction = (e, brandId, name) => {
    e.preventDefault();
    const confirmDelete = window.confirm(`You are about to Delete ${name} and it will not be able to be retrieved. Are you sure?`);
    if (confirmDelete) {
      axios.delete(`/brand/delete/${brandId}`, {
        headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }
      })
      .then((response) => {
        // Reload the page or update state as needed
        window.location.reload();
      })
      .catch((error) => {
        console.error('Error deleting product:', error);
      });
    } else {
      // Handle cancellation if needed
      console.log('Deletion canceled');
    }
  }

  const columns = [
    {
      key: 'name',
      label: 'Name',
    },
    {
      key: 'logo',
      label: 'Logo',
    },
    {
      key: 'actions',
      label: 'Actions',
      filter: false,
      sorter: false,
      _style: { width: '20%' }

    },
  ]

  return (
    <>
    <div className="bg-body-tertiary min-vh-100 d-flex flex-row align-items-center">
      <CContainer>
        <CRow className="justify-content-center">
        <CCol xl={12}>
          <CCard className="mb-4">
            <CCardBody className="p-4">
        <CRow className='p-4'>
          <CCol sm={5}><h4>Brands</h4></CCol>
          <CCol sm={7} className='d-none d-md-block'>
          <Link to="/addBrand">
            <CButton className='btn btn-primary float-end' type='button'>Add Brand</CButton>
          </Link> 
          </CCol>
        </CRow>
              <CCol md={12}>
              <CSmartTable
        activePage={2}
        cleaner
        clickableRows
        columns={columns}
        columnFilter
        columnSorter
        items={brands}
        itemsPerPageSelect
        itemsPerPage={5}
        pagination
        onFilteredItemsChange={(items) => {
          console.log(items)
        }}
        onSelectedItemsChange={(items) => {
          console.log(items)
        }}
        scopedColumns={{
          actions: (item) => (
            <td>
              <div onClick={(e) => navigate("/editBrand", {state:{brandId: item.id}})}>
                <CIcon icon={cilPen} className="me-2" />
              </div>
              { (item.permission)?
                <CIcon icon={cilDelete} className="me-2" onClick={(e)=>deleteBrandAction(e, item.id, item.name)} />:''}
                
            </td>
          ),
        }}    
        selectable
        sorterValue={{ column: 'createdAt', state: 'desc' }}
        tableFilter
        tableProps={{
          className: 'add-this-class',
          responsive: true,
          striped: true,
          hover: true,
        }}
        tableBodyProps={{
          className: 'align-middle'
        }}
      />
              </CCol>
            </CCardBody>
          </CCard>
        </CCol>
        </CRow>
      </CContainer>
    </div>
    </>
  )
}

export default Brands
